@import "styles/mixins";
.not-found {
  margin: 77px auto 0 auto;
  &__div {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
  }
  &__p {
    font-size: 24px;
    margin-bottom: 16px;
    color: $font-color-accent;
  }
}
